/* Whole page black background */
.leader-page {
  background-color: #000000;
  min-height: 100vh;
  padding: 2rem;
  font-family: "Inter var", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  color: white;
}

/* Container for profile content */
.leader-profile-container {
  max-width: 900px;
  margin: 0 auto;
  padding: 2rem;
  background-color: #111111;
  border-radius: 20px;
  box-shadow: 0 8px 25px rgba(0,0,0,0.7);
}

/* Profile Header */
.leader-header {
  text-align: center;
}

.leader-photo {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 1rem;
  border: 3px solid #14097a;
}

.leader-name {
  font-size: 28px;
  font-weight: 800;
  margin: 0.5rem 0;
}

.leader-role {
  font-size: 16px;
  color: #ccc;
}

/* Social Links */
.social-links a {
  margin: 0 10px;
  color: #380c84;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s;
}

.social-links a:hover {
  color: #FF3CAC;
}

/* Summary */
.leader-summary {
  margin-top: 2rem;
}

.leader-summary h2 {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 0.5rem;
  background-color: #060d79;
 
}

.leader-summary p {
  font-size: 16px;
  line-height: 1.6;
  color: #ddd;
}

/* Comments */
.leader-comments {
  margin-top: 2rem;
}

.leader-comments h2 {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 1rem;
}

.leader-comments ul {
  list-style: none;
  padding: 0;
  margin-bottom: 1rem;
}

.leader-comments li {
  padding: 0.5rem 0;
  border-bottom: 1px solid #222;
  color: #eee;
}

.comment-form textarea {
  width: 100%;
  padding: 10px;
  border-radius: 12px;
  border: none;
  background-color: #222;
  color: white;
  resize: none;
  font-size: 14px;
  margin-bottom: 10px;
}

.comment-form button {
  padding: 10px 20px;
  border-radius: 12px;
  border: none;

    background: linear-gradient(135deg, #051aa3, #010307);
  
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.comment-form button:hover {
  transform: translateY(-3px);
}
